import React, { createContext, useContext, useState, useEffect, useCallback } from 'react';
import { io } from 'socket.io-client';

const SocketContext = createContext();

export const useSocket = () => useContext(SocketContext);

export const SocketProvider = ({ children }) => {
  const [socket, setSocket] = useState(null);
  const [connected, setConnected] = useState(false);
  const [user, setUser] = useState(() => {
    //     
    const savedUser = localStorage.getItem('user');
    return savedUser ? JSON.parse(savedUser) : null;
  });
  
  //  
  useEffect(() => {
    //      
    const newSocket = process.env.NODE_ENV === 'production'
      ? io() //    
      : io(process.env.REACT_APP_SERVER_URL || 'http://localhost:3002'); //     
    
    newSocket.on('connect', () => {
      console.log(' ');
      setConnected(true);
      
      //   ,     
      const savedUser = localStorage.getItem('user');
      if (savedUser) {
        const userData = JSON.parse(savedUser);
        if (userData && userData.nickname) {
          console.log(' ,   :', userData.nickname);
          newSocket.emit('join-lobby', userData.nickname);
        }
      }
    });
    
    newSocket.on('disconnect', () => {
      console.log('  ');
      setConnected(false);
    });
    
    setSocket(newSocket);
    
    //      
    return () => {
      newSocket.disconnect();
    };
  }, []);
  
  //       
  useEffect(() => {
    if (user) {
      localStorage.setItem('user', JSON.stringify(user));
    }
  }, [user]);
  
  //  
  const joinLobby = useCallback((nickname) => {
    if (socket && connected) {
      console.log(`Joining lobby with nickname: ${nickname}`);
      socket.emit('join-lobby', nickname);
      
      //   
      const lobbyJoinedHandler = (userData) => {
        console.log('Lobby joined with user data:', userData);
        const userInfo = {
          id: userData.id,
          nickname: userData.nickname
        };
        setUser(userInfo);
        localStorage.setItem('user', JSON.stringify(userInfo));
      };
      
      //    
      socket.off('lobby-joined');
      //   
      socket.on('lobby-joined', lobbyJoinedHandler);
    }
  }, [socket, connected]);
  
  //  
  const createRoom = useCallback((options = {}) => {
    if (socket && connected) {
      socket.emit('create-room', options);
    }
  }, [socket, connected]);
  
  //  
  const joinRoom = useCallback((roomId) => {
    if (socket && connected) {
      socket.emit('join-room', roomId);
    }
  }, [socket, connected]);
  
  //  
  const typeWord = useCallback((word) => {
    if (socket && connected) {
      socket.emit('type-word', { word });
    }
  }, [socket, connected]);
  
  // 
  const logout = useCallback(() => {
    setUser(null);
    localStorage.removeItem('user');
  }, []);
  
  //  context 
  const value = {
    socket,
    connected,
    user,
    joinLobby,
    createRoom,
    joinRoom,
    typeWord,
    logout
  };
  
  return (
    <SocketContext.Provider value={value}>
      {children}
    </SocketContext.Provider>
  );
}; 